import styled from 'styled-components'
import {MdEmail} from 'react-icons/md'
import {FaGithub, FaPhoneAlt, FaLinkedin} from 'react-icons/fa'

export const EmailLink = styled (MdEmail)`
font-size:25px;
color:#7c5295;

&:hover{
     color:#3c1361;
    transition:all 0.2s ease-in-out
}
`
export const GitLink = styled (FaGithub)`
font-size:25px;
color:#7c5295;

&:hover{
     color:#3c1361;
    transition:all 0.2s ease-in-out
}
`
export const PhoneLink = styled (FaPhoneAlt)`
font-size:18px;
color:#7c5295;

&:hover{
     color:#3c1361;
    transition:all 0.2s ease-in-out
}
`

export const LinkedInIcon = styled (FaLinkedin)`
font-size:25px;
color:#7c5295;

&:hover{
     color:#3c1361;
    transition:all 0.2s ease-in-out
}
`