import React, { Component } from "react";

import {
  Icon,
  Navbar,
  NavHeader,
  NavHeaderLink,
  NavItem,
  NavLink,
  Navlist,
  ToggleIcon,
} from "./HeaderComponent";
import "bootstrap/dist/css/bootstrap.min.css";
import "../CSS/style.css";

export class Header extends Component {
  render() {
    return (
      <div>
        <Navbar className="shadow-lg" isOpen={this.props.isOpen}>
          <NavHeader>
            <NavHeaderLink>TNP Cell, CITK</NavHeaderLink>
          </NavHeader>

          <ToggleIcon onClick={this.props.toggle}>
            <Icon onClick={this.props.toggle}></Icon>
          </ToggleIcon>

          <Navlist>
            <NavItem>
              <NavLink to="/">Home</NavLink>
            </NavItem>
            <NavItem>
              <NavLink to="/about">About</NavLink>
            </NavItem>
            <NavItem>
              <NavLink to="/contact">Contact</NavLink>
            </NavItem>
            <NavItem>
              <NavLink
                to="/placement"
                
              >
                Placements
              </NavLink>
            </NavItem>
            <NavItem>
              <NavLink to="/activities">Activities</NavLink>
            </NavItem>
          </Navlist>
          <Navlist>
            <NavItem>
              <NavLink>Sign Up</NavLink>
            </NavItem>
          </Navlist>
        </Navbar>
      </div>
    );
  }
}

export default Header;
