import React, { Component } from "react";
import {
  Icon,
  SideBar,
  SideBarContent,
  SideBarItems,
  SideBarLink,
} from "./SidebarElements";

export class Sidebar extends Component {
  render() {
    return (
      <div>
        <SideBar isOpen={this.props.isOpen} onClick={this.props.toggle}>
          <Icon onClick={this.props.toggle}></Icon>

          <SideBarContent>
            <SideBarItems>
              <SideBarLink to="/" onClick={() => this.props.toggle}>
                Home
              </SideBarLink>
              <SideBarLink to="/about" onClick={this.props.toggle}>
                About
              </SideBarLink>
              <SideBarLink to="/contact" onClick={this.props.toggle}>
                Contact
              </SideBarLink>
              <SideBarLink to="/join" onClick={this.props.toggle}>
                Join
              </SideBarLink>
              <SideBarLink to="/signup" onClick={this.props.toggle}>
                SignUp
              </SideBarLink>
            </SideBarItems>
          </SideBarContent>
        </SideBar>
      </div>
    );
  }
}

export default Sidebar;
