import styled from 'styled-components'
import{FaTimes } from 'react-icons/fa'
import { Link } from 'react-router-dom'

export const SideBar=styled.div`
background-color:#52307c;
 height:100vh;
 z-index:10;
 
 display:${({isOpen})=>(isOpen?'inherit':'none')};
 

@media screen and (min-width:600px){
    display:none;
}

     animation-name:${({isOpen})=>(isOpen?'toggleright':'none')};
     animation-duration:1s;

     
     animation-fill-mode:forwards;

     /* @keyframes toggleleft{
         from{
             width:100%;
         }
         to{
             width:0%; display: none;
             
         }
     } */
      @keyframes toggleright{
         from{
             width:0%;
         }
         to{
             width:100%;
             
         }
     }

 
 
`
export const SideBarContent=styled.div`
  
  opacity: 0.7;
  height:100%;
  display:flex;
  justify-content:center;
    
`

export const SideBarItems = styled.ul`
    color:#fff;
    margin-top: 50px;
    display:grid;
    
    
    grid-template-columns:auto;

    grid-template-rows: 5vh 5vh 5vh 5vh  ;

    grid-gap:10vh;
    
`

export const SideBarLink = styled(Link)`
    color:#fff;
    list-style-type:none;
    font-size:20px;
    cursor:pointer;
    text-align:center;
    text-decoration:none!important;


     &:hover{
        
        transition:all 0.2s ease-in-out;
        color:#bca0dc;
    }
    
    &:active{
        border-bottom: 1px solid #bca0dc;
    }
    

`

export const Icon=styled(FaTimes)`
    color:#fff;
     font-size:25px;
     cursor:pointer;
     float:right;
     margin-right: 20px;
     margin-top: 20px;
     
`

