import React, {useState} from 'react'

import Header from '../Header/Header'
import Sidebar  from '../Sidebar/Sidebar'
import Hero from '../Hero/Hero'
import Footer from '../Footer/Footer'
import { BrowserRouter as Router, Switch, Route } from 'react-router-dom'
import About from '../AboutSection/About'
import Contact from '../ContactSection/Contact'
import Placement from '../PlacementSection/Placement'
import Activities from '../ActivitiesSection/Activities'




const Home = () => {

     const[isOpen, setIsOpen]=useState(false)

    const toggle=()=>[
        setIsOpen(!isOpen),
        console.log('works bro')
    ]

    const propy='pass';
    return (
       <Router>
            <div>
             <Header toggle={toggle}pass={propy} isOpen={isOpen}></Header>
                    <Sidebar toggle={toggle}pass={propy} isOpen={isOpen}></Sidebar>
                     <div className="content">
                         <Switch>
                              <Route exact path='/'>
                                    <Hero/>
                              </Route>
                              <Route exact path='/about'>
                                    <About/>
                              </Route>
                              <Route exact path='/placement'>
                                    <Placement/>
                              </Route>
                              <Route exact path='/contact'>
                                    <Contact/>
                              </Route>
                              <Route exact path='/about'>
                                    <About/>
                              </Route>
                              <Route exact path='/activities'>
                                    <Activities/>
                              </Route>
                               
                         </Switch>
                     </div>
              <Footer  isOpen={isOpen}></Footer>
       
        </div>
       </Router>
    )
}

export default Home



